.footer{
    margin: 10px auto;
    display: flex;
    justify-content: space-between;
    padding: 15px 0;
    border-top: 1px solid #676767;
}
.footer ul li{
    list-style: none;
    display: inline-block;
    margin-left: 20px;
}
/* media query */
@media(max-width:800px){
      .footer{
        display: block;
        text-align: center;
      }
      .footer ul {
        margin-top: 10px;
      }
      .footer ul li{
        padding: 20px;
        text-align: center;
      }
}