 .about{
    margin: 100px auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 90%;
}
.about-left{
    flex-basis: 40%;
    position: relative;
}
.about-right{
    flex-basis: 56%;
}
.about-image{
    width: 100%;
    border-radius: 10px;
}
.play-icon{
    width: 60px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50% );
    cursor: pointer;
}
.about h3{
    font-weight: 600;
    font-weight: 16px;
    color: #212EA0;
}
.about h2{
    font-size: 35px;
    color-interpolation-filters: #000F38;
    margin: 10px 0;
    max-width: 400px;
}
.about p {
    color: #676767;
    margin-bottom: 15px;
}

/* media query */
@media(max-width: 800px){
    .about{
        flex-direction: column;
    }
    .about-left{
        flex-basis: 100%;
        margin: 20px;
    }
    .about-right{

        flex-basis: 100%;
    }
}
