.campus{
     margin: 80px auto;
     width: 90%;
     text-align: center;
}
.gallery{
      display: flex; 
     align-items: center;
     justify-content: space-between; 
     margin-bottom: 40px;
}
.gallery img{
     width: 23%;
     border-radius: 13px;
}
.sbtn{
    background: #212EA0;
    color: #fff;
    border-radius: 20px;
    text-align: center;
    padding: 10px 20px;
    font-size: 16px;
}

/* media query */

@media(max-width:800px){
    .gallery{
     flex-wrap: wrap;
    }
    .gallery img{
     width: 48%;
     margin-bottom: 25px;
    }
}