nav{
   width: 100%;
   color: #fff;
   padding: 4px 0;
   position: fixed;
   top: 0;
   left: 0;
   display: flex;
   align-items: center;
   justify-content: space-between;
   z-index: 10;
   transition: 1s;

}
.logo{
    width: 20%; 
}
nav ul li{
    display: inline-block;
    list-style-type: none;
    margin: 5px 20px;
    font-size: 16px;
}
.container{
    padding-left: 10%;
    padding-right: 10%;
}
.btn{
   background: #FFF;
   color: #212121;
   padding: 10px 20px;
   font-size: 16px;
   border-radius: 30px;
   cursor: pointer;
   border: 0;
   outline: 0;
}
.dark-nav{
    background: #212EA0;
    transition: 1s;
}
.menu-icon{
    display: none;
}






/* media query */
@media (max-width:1000px){
    .logo{
        width: 140px;
    }
    nav ul li{
        margin: 10px 15px;
    }
}
@media (max-width : 840px) {
    nav{
        padding: 15px 0;
    }
    nav ul{
        position: fixed;
        top: 0;
        right: 0px;
        bottom: 0;
        background: #212EA0;
        z-index: -1;
        width: 200px;
        padding-top: 70px;
        transition: 0.5s;
    }
    nav ul li{
        display: block;
        margin: 25px 40px;

    }

    .menu-icon{
        display: block;
        width: 30px;
        cursor: pointer;
        margin-right: -70px;
    }
    .hide-mobile-menu{
        right: -200px;
    }
}